import { createApp } from 'vue'
import App from './App.vue'
import '../enftx/css/style.css'
import Admin from "@/components/Admin";
import {createRouter, createWebHistory} from "vue-router";
import Shorai from "@/components/Shorai";
import Pixelguy from "@/components/Pixelguy";

export let app = createApp(App);

const routes = [
    { path: '/shorai', component: Shorai },
    { path: '/pixelguy', component: Pixelguy },
    { path: '/admin', component: Admin },
    { path: '/', component: Pixelguy },
];

const router = createRouter({
    history: createWebHistory(),
    routes,
})

app.use(router);

app.mount('#app')
