<template>
  <div class="content-body">
    <div class="container text-start">
      <div class="row">
        <div class="col-xxl-6">
          <div class="promotion d-flex justify-content-between align-items-center">
            <div class="promotion-detail">
              <h3 class="text-white mb-3">Pixel Guy Club</h3>
              <p>The cool little guys, straight out of that square box you call a computer, were created by us there and are now coming to life. Collect as much as you can!</p>
              <a href="https://oasis.cash/collection/0xC995199DC53922caCE4f6ac14A476eF8c9429387" target="_blank"  class="btn btn-secondary me-3">Buy on Oasis</a>
            </div>
          </div>
        </div>
        <div class="col-xxl-6">
          <div class="card top-bid">
            <div class="card-body">
              <div class="row align-items-center">
                <div class="col-md-6"><img src="/images/avatar/pixel-guy.png" class="img-fluid rounded"
                                           alt="..."></div>
                <div class="col-md-6">
                  <h4 class="card-title">Balance: {{(Math.round(myBalance * 100000) / 100000)}}</h4>
                  <div class="d-flex justify-content-between power-and-rate">
                    <div class="text-start">
                      <h5>My power: {{myPower / 10000}}</h5><br />
                      <p class="mb-2" v-if="earningRate && allocatedPower && myPower">Earning rate: ~{{(Math.round(earningRate / allocatedPower * myPower * 100000) / 100000)}} NFTC / hour</p>
                    </div>
                  </div>
                  <div class="d-flex">
                    <button v-if="!harvestDisabled" @click="harvest" class="btn btn-primary harvest-button">Harvest {{earned}} NFTC</button>
                    <button v-if="harvestDisabled" class="btn btn-primary harvest-button" disabled>Harvest {{earned}} NFTC</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <Nft v-bind:contract="nftContract" projectname="Pixel Guy Club" projectbaseuri="https://raw.githubusercontent.com/NftClubCash/PixelGuyClub/main/png/" />

  </div>
</template>

<script>
import {ethers} from "ethers";
import NftStake from '../../artifacts/contracts/NftStake.sol/NftStake.json';
import SimpleToken from '../../artifacts/contracts/SimpleToken.sol/SimpleToken.json';
import Nft from '@/components/Nft.vue';

let TokenAddress = window.TokenAddress;
let StakingAddress = window.StakingAddress;

export default {
  name: 'App',
  components: {Nft},
  data() {
    return {
      'nftContract': window.PixelguyAddress,
      'token': null,
      'nft': null,
      'staking': null,
      'power': null,
      'provider': null,
      'signer': null,
      'allocatedPower': null,
      'released': null,
      'liquidity': null,
      'earned': null,
      'unallocatedEarned': null,
      'myPower': null,
      'earningRate': null,
      'myBalance': 0,
      'harvestDisabled': true,
    }
  },
  methods: {
    harvest: async function() {
      let tx = await this.staking.harvest();
      await tx.wait();
      window.location.reload();
    },
    approve: async function () {
      let tx = await this.nft.setApprovalForAll(StakingAddress, true);
      await tx.wait();
      window.location.reload();
    },
    load: function() {
      this.signer.getAddress().then((address) => {
        this.token.balanceOf(address).then((balance) => {
          this.myBalance = ethers.utils.formatEther(balance);
        });
      });
    }
  },
  mounted: function () {
    this.ca = this.$route.params.ca;

    this.shoraiCA = window.ShoraiAddress;
    this.pixelguyCA = window.PixelguyAddress;

    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();
      let tokenContract = new ethers.Contract(TokenAddress, SimpleToken.abi, this.provider.getSigner());
      let stakingContract = new ethers.Contract(StakingAddress, NftStake.abi, this.provider.getSigner());
      this.token = tokenContract;
      this.staking = stakingContract;
    }).then(() => {
      this.load();
      this.staking.earnableTokens().then((amount) => {
        this.liquidity = ethers.utils.formatEther(amount);
      });
      this.staking.allocatedPower().then((power) => {
        this.allocatedPower = power;
      });
      this.staking.released().then((amount) => {
        this.released = ethers.utils.formatEther(amount);
      });
      this.staking.getEarningPerHour().then((amount) => {
        this.earningRate = (Math.round(ethers.utils.formatEther(amount) * (1 - window.DevCut) * 100000) / 100000);
      });
      this.signer.getAddress().then((userAddress) => {
        this.staking.earningsWoDev(userAddress).then((earnedSoFar) => {
          this.earned = (Math.round(ethers.utils.formatEther(earnedSoFar) * 100000) / 100000);
          if(earnedSoFar > window.minHarvest) {
            this.harvestDisabled = false;
          }
        });
        this.staking.stakers(userAddress).then((staker) => {
          this.myPower = staker.power;
        });
      });
    });
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 60px;
}
</style>
