<template>
  <template v-if="transactionInProgress">
    <div class="container">
      <div class="row">
        <div class="col-12 text-center">
          Transaction in progress. Please wait...
        </div>
      </div>
    </div>
  </template>

  <template v-if="loadingStakedNfts === true && loadingUnstakedNfts === true">
    <div class="text-center">Loading...</div>
  </template>

  <template v-if="approved === false">
    <div class="text-center">
      <button @click="approve" class="btn btn-primary">Approve contract</button>
    </div>
  </template>

  <template v-if="approved">
    <div class="container">
      <div class="row">
        <div class="col-6">
          <h2 class="text-start">{{ projectname }}</h2>
          <div class="row">
            <div class="col-lg-4 col-sm-12 col-xs-12">
              <ul class="settings-menu">
                <button
                  class="btn btn-primary"
                  type="button"
                  id="collection-filter"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  Filter
                </button>
                <ul class="dropdown-menu" aria-labelledby="collection-filter">
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="filter = null"
                      v-bind:class="filter === null ? 'current' : ''"
                      >All ({{ nfts.length + staked.length }})</a
                    >
                  </li>
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="filter = 'staked'"
                      v-bind:class="filter === 'staked' ? 'current' : ''"
                      >Staked ({{ staked.length }})</a
                    >
                  </li>
                  <li>
                    <a
                      class="dropdown-item"
                      href="#"
                      @click="filter = 'unstaked'"
                      v-bind:class="filter === 'unstaked' ? 'current' : ''"
                      >Unstaked ({{ nfts.length }})</a
                    >
                  </li>
                </ul>
              </ul>
            </div>
          </div>
        </div>
        <div class="col-6">
          <div class="d-grid gap-2 d-md-block text-end">
            <button @click="stakeAll" class="btn btn-success btn-lg">
              Stake all (30 max)
            </button>
            &nbsp;
            <button @click="unstakeAll" class="btn btn-danger btn-lg">
              Unstake all (30 max)
            </button>
          </div>
        </div>
      </div>
      <div class="col-12">
        <div class="card filter-tab">
          <div class="card-body bs-0 p-0 bg-transparent">
            <div class="row">
              <template
                v-if="filter == null || filter == '' || filter == 'staked'"
              >
                <div
                  class="col-xxl-3 col-xl-6 col-lg-6 col-md-6 col-sm-6"
                  v-for="nft in staked"
                  :key="nft.tokenId"
                >
                  <div class="card items">
                    <div class="card-body">
                      <div class="items-img position-relative">
                        <img
                          v-bind:src="nft.image"
                          class="img-fluid rounded mb-3"
                          alt=""
                        />
                      </div>
                      <h4 class="card-title">
                        #{{ nft.tokenId }} ⚡{{ nft.power / 10000 }}
                      </h4>
                      <div class="d-flex justify-content-center mt-3">
                        <button
                          @click="unstake(nft.ca, nft.tokenId)"
                          class="btn btn-danger"
                        >
                          Unstake
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
              <template
                v-if="filter == null || filter == '' || filter == 'unstaked'"
              >
                <div
                  class="col-xxl-3 col-xl-6 col-lg-6 col-md-6 col-sm-6"
                  v-for="nft in nfts"
                  :key="nft.tokenId"
                >
                  <div class="card items">
                    <div class="card-body">
                      <div class="items-img position-relative">
                        <img
                          v-bind:src="nft.image"
                          class="img-fluid rounded mb-3"
                          alt=""
                        />
                      </div>
                      <h4 class="card-title">
                        #{{ nft.tokenId }} ⚡{{ nft.power / 10000 }}
                      </h4>
                      <div class="d-flex justify-content-center mt-3">
                        <button
                          @click="stake(nft.ca, nft.tokenId)"
                          class="btn btn-primary"
                        >
                          Stake
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </template>
              <template
                v-if="
                  approved !== null &&
                  loadingStakedNfts === false &&
                  loadingUnstakedNfts === false &&
                  staked.length === 0 &&
                  nfts.length === 0
                "
              >
                <div class="text-center">No NFTs to display</div>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
  </template>
</template>


<script>
const Web3 = require("web3");
import { ethers } from "ethers";
import NftStake from "../../artifacts/contracts/NftStake.sol/NftStake.json";
import SimpleNFT from "../../artifacts/contracts/SimpleNFT.sol/SimpleNFT.json";
import NftPower from "../../artifacts/contracts/NftPower.sol/NftPower.json";

import shoraiInitialStakes from "../shorai-initial-stakes.json";
import pixelguyInitialStakes from "../pixelguy-initial-stakes.json";

// import shoraiInitialStakesTest from "../shoraiTest-initial-stakes.json";
// import pixelguyInitialStakesTest from "../pixelguyTest-initial-stakes.json";

import shoraiRarities from "../shorai-rarity.json";
import pixelguyRarities from "../pixelguy-rarity.json";

var pixelguy = {
  contractAddress: "0xC995199DC53922caCE4f6ac14A476eF8c9429387",
  name: "Pixel Guy",
  shortName: "pixelguy",
  rarities: pixelguyRarities,
    nextBlockToCheck: pixelguyInitialStakes.nextBlockToCheck,
  initialStakes: pixelguyInitialStakes,
  pictureLink:
    "https://raw.githubusercontent.com/NftClubCash/PixelGuyClub/main/png/",
  rpcs: [
    "https://smartbch.fountainhead.cash/mainnet",
    "https://smartbch.greyh.at",
    "https://smartbch.electroncash.de/",
    "https://global.uat.cash",
    "https://node.1bch.com:8443",
  ],
  rpcIndex: 1,
};

var shorai = {
  contractAddress: "0xf928B82061bc531e3B24B8CF3736724e35988b7a",
  name: "Shorai",
  shortName: "shorai",
  rarities: shoraiRarities,
  nextBlockToCheck: shoraiInitialStakes.nextBlockToCheck,
  initialStakes: shoraiInitialStakes,
  pictureLink: "https://nftraritycash.github.io/nftassets/shorai-optimized/",
  rpcs: [
    "https://smartbch.fountainhead.cash/mainnet",
    "https://smartbch.greyh.at",
    "https://smartbch.electroncash.de/",
    "https://global.uat.cash",
    "https://node.1bch.com:8443",
  ],
  rpcIndex: 1,
};

// var pixelguyTest = {
//   contractAddress: "0x3c4f773efe8b0Fc7f0c10CaCd074429536607205",
//   name: "Pixel Guy Test",
//   shortName: "pixelguyTest",
//   rarities: pixelguyRarities,
//   nextBlockToCheck: pixelguyInitialStakesTest.nextBlockToCheck,
//   initialStakes: pixelguyInitialStakesTest,
//   pictureLink:
//     "https://raw.githubusercontent.com/NftClubCash/PixelGuyClub/main/png/",
//   rpcs: ["https://moeing.tech:9545", "http://35.220.203.194:8545"], // Testnet
//   rpcIndex: 0,
// };

// var shoraiTest = {
//   contractAddress: "0xbda792DcBEEFFbB066879AB9EF4A444eD6f72729",
//   name: "Shorai Test",
//   shortName: "shoraiTest",
//   rarities: shoraiRarities,
//   nextBlockToCheck: shoraiInitialStakesTest.nextBlockToCheck,
//   initialStakes: shoraiInitialStakesTest,
//   pictureLink: "https://nftraritycash.github.io/nftassets/shorai-optimized/",
//   rpcs: ["https://moeing.tech:9545", "http://35.220.203.194:8545"], // Testnet
//   rpcIndex: 0,
// };

const nftCollection = {
  "0xf928B82061bc531e3B24B8CF3736724e35988b7a": shorai,
  "0xC995199DC53922caCE4f6ac14A476eF8c9429387": pixelguy,
  // "0x3c4f773efe8b0Fc7f0c10CaCd074429536607205": pixelguyTest,
  // "0xbda792DcBEEFFbB066879AB9EF4A444eD6f72729": shoraiTest,
};

let PowerAddress = window.PowerAddress;
let StakingAddress = window.StakingAddress;

function removeItemOnce(arr, value) {
  var index = arr.indexOf(value);
  if (index > -1) {
    arr.splice(index, 1); // 2nd parameter: Amount of elements to remove
  } else {
    console.log("Unstaking an NFT that has not been staked before!");
  }
  return arr;
}

export default {
  name: "App",
  props: ["contract", "projectname", "projectdescription", "projectbaseuri"],
  data() {
    return {
      filter: null,
      token: null,
      nft: null,
      staking: null,
      power: null,
      provider: null,
      signer: null,
      loadingUnstakedNfts: false,
      loadingStakedNfts: false,
      nfts: [],
      staked: [],
      allocatedPower: null,
      released: null,
      liquidity: null,
      earned: null,
      unallocatedEarned: null,
      myPower: null,
      earningRate: null,
      approved: null,
      myBalance: 0,
      imageurl: "",
      transactionInProgress: false,
    };
  },
  methods: {
    approve: async function () {
      this.transactionInProgress = true;
      let tx = await this.nft.setApprovalForAll(StakingAddress, true, {
        gasLimit: 60000,
        gasPrice: 1046760000,
      });
      await tx.wait();
      window.location.reload();
    },
    stake: async function (ca, tokenId) {
      this.transactionInProgress = true;
      let tx = await this.staking.enter([ca], [tokenId], {
        gasLimit: 5000000,
        gasPrice: 1046760000,
      });
      // let tx = await this.staking.enter([ca], [tokenId]);
      await tx.wait();
      window.location.reload();
    },
    unstake: async function (ca, tokenId) {
      this.transactionInProgress = true;
      let tx = await this.staking.leave([ca], [tokenId], {
        gasLimit: 5000000,
        gasPrice: 1046760000,
      });
      await tx.wait();
      window.location.reload();
    },
    stakeAll: async function () {
      this.transactionInProgress = true;
      var CAs = [];
      var IDs = [];

      for (var i in this.nfts.slice(0, 30)) {
        CAs.push(this.nfts[i].ca);
        IDs.push(this.nfts[i].tokenId);
      }

      if (IDs.length === 0) {
        alert(
          "No NFTs were loaded yet. We are currently experiencing loading problems. Sorry for any inconvenience. Please try again later."
        );
      }

      let tx = await this.staking.enter(CAs, IDs, {
        gasLimit: Math.min(4500000 + 500000 * CAs.length, 9999999),
        gasPrice: 1046760000,
      });
      await tx.wait();
      window.location.reload();
    },
    unstakeAll: async function () {
      this.transactionInProgress = true;
      var CAs = [];
      var IDs = [];

      for (var i in this.staked.slice(0, 30)) {
        CAs.push(this.staked[i].ca);
        IDs.push(this.staked[i].tokenId);
      }

      if (IDs.length === 0) {
        alert(
          "No NFTs were loaded yet. We are currently experiencing loading problems. Sorry for any inconvenience. Please try again later."
        );
      }

      let tx = await this.staking.leave(CAs, IDs, {
        gasLimit: Math.min(4500000 + 500000 * CAs.length, 9999999),
        gasPrice: 1046760000,
      });
      await tx.wait();
      window.location.reload();
    },
    load: function () {
      this.loadingUnstakedNfts = true;
      this.loadingStakedNfts = true;
      this.signer
        .getAddress()
        .then(async (myAddress) => {
          // this.nft.walletOfOwner(myAddress).then((tokens) => {
          //   for(var i in tokens) {
          //     let tokenId = tokens[i];
          //     this.power.power(this.contract, tokenId).then((power) => {
          //       this.nfts.push({
          //         ca: this.contract,
          //         tokenId: tokenId,
          //         power: power,
          //         image: pixelguy.pictureLink  + tokenId + '.png',
          //       });
          //     });
          //   }
          // });
          // this.nft.balanceOf(StakingAddress).then((balance) => {
          //   for(var i = 0;i < balance;i++) {
          //     this.nft.tokenOfOwnerByIndex(StakingAddress, i).then((tokenId) => {
          //       this.staking.nfts(this.contract.toString(), tokenId).then((nftData) => {
          //         if (nftData.owner === myAddress) {
          // this.staked.push({
          //   ca: this.contract.toString(),
          //   tokenId: tokenId,
          //   power: nftData.power,
          //   image: this.projectbaseuri + tokenId + '.png',
          // });
          //         }
          //       });
          //     });
          //   }
          // });

          var currentCollection = nftCollection[this.contract];
          // console.log(this.contract + " " + currentCollection.name);
          // copying staked NFTs from snapshot
          var myStakedNfts = [];
          if (myAddress in currentCollection.initialStakes) {
            myStakedNfts = await currentCollection.initialStakes[
              myAddress
            ].slice();
          }
          // console.log(myStakedNfts);

          // loading stakes and unstakes that came later
          // console.log("nextBlock: " + currentCollection.initialStakes.nextBlockToCheck);
          const options = {
            // filter: { to: [ staking.contractAddress ] },
            fromBlock: currentCollection.initialStakes.nextBlockToCheck, //Number || "earliest" || "pending" || "latest"
            toBlock: "latest",
          };

          // try {
          var web3Http = new Web3(
            currentCollection.rpcs[currentCollection.rpcIndex]
          );
          const mintContractHttp = new web3Http.eth.Contract(
            SimpleNFT.abi,
            currentCollection.contractAddress
          );

          console.log("Getting past events");
          mintContractHttp
            .getPastEvents("allEvents", options)
            .then(function (events) {
              if (0 >= events.length) {
                console.log("No events");
                return true;
              }
              // currentCollection.initialStakes.nextBlockToCheck = Math.max(currentCollection.initialStakes.nextBlockToCheck, (events[events.length - 1].blockNumber + 1));

              for (let item of events) {
                // console.log(item.returnValues);
                let id = item.returnValues.tokenId;
                let from = item.returnValues.from;
                let to = item.returnValues.to;

                // staking
                if (from == myAddress && to == StakingAddress) {
                  if (myStakedNfts.includes(id)) {
                    console.log(
                      "Trying to stake an NFT that is already staked!"
                    );
                  } else {
                    myStakedNfts.push(id);
                    // console.log(myStakedNfts);
                  }
                  // console.log(item.blockNumber + ": " + from + " staked " + id);
                }
                // unstaking
                if (to == myAddress && from == StakingAddress) {
                  removeItemOnce(myStakedNfts, id);
                  // console.log(myStakedNfts);

                  // console.log(item.blockNumber + ": " + to + " unstaked " + id);
                }
              }
            })
            .then(() => {
              for (let i = 0; i < myStakedNfts.length; i++) {
                let tokenId = myStakedNfts[i];
                // console.log(i + " " + tokenId);
                this.staked.push({
                  ca: currentCollection.contractAddress,
                  tokenId: tokenId,
                  power: currentCollection.rarities.power[tokenId - 1],
                  image: currentCollection.pictureLink + tokenId + ".png",
                });
              }
            })
            .then(() => {
          this.loadingStakedNfts = false;
        });
          // .catch(function (error) {
          //   console.log(error);
          // });
          // } catch (e) {
          //   console.log(e);
          // }

          this.nft.walletOfOwner(myAddress).then((tokens) => {
            for (var i in tokens) {
              let tokenId = tokens[i];
              this.nfts.push({
                ca: this.contract,
                tokenId: tokenId,
                power: currentCollection.rarities.power[tokenId - 1],
                image: currentCollection.pictureLink + tokenId + ".png",
              });
            }
          }).then(() => {
          this.loadingUnstakedNfts = false;
        });
        });
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        let nftContract = new ethers.Contract(
          this.contract.toString(),
          SimpleNFT.abi,
          this.provider.getSigner()
        );
        let powerContract = new ethers.Contract(
          PowerAddress,
          NftPower.abi,
          this.provider.getSigner()
        );
        let stakingContract = new ethers.Contract(
          StakingAddress,
          NftStake.abi,
          this.provider.getSigner()
        );

        this.nft = nftContract;
        this.staking = stakingContract;
        this.power = powerContract;
      })
      .then(() => {
        this.load();
        this.signer.getAddress().then((myAddress) => {
          this.nft
            .isApprovedForAll(myAddress, StakingAddress)
            .then((approved) => {
              this.approved = approved;
            });
        });
      });
  },
};
</script>
