<template>
  <div class="header">
      <div class="container">
          <div class="row">
              <div class="col-xxl-12">
                  <div class="header-content">
                      <div class="header-left">
                          <div class="brand-logo">
                              <a class="mini-logo" href="./">
                                <img src="images/logoi.png" alt="" width="40">
                              </a>
                          </div>
                      </div>
                      <div class="header-right">
                          <div class="dark-light-toggle theme-switch" onclick="themeToggle()">
                              <span class="dark"><i class="ri-moon-line"></i></span>
                              <span class="light"><i class="ri-sun-line"></i></span>
                          </div>
                          <div class="dark-light-toggle theme-switch">
                              <a @click="addToMM" class="btn btn-outline-secondary btn-sm"><img src="images/metamask-icon-32.png" /> Add NFTC</a>
                          </div>
                          <div class="dark-light-toggle theme-switch" v-if="!myAddress && !wrongNetwork">
                              <a @click="loginMM" class="btn btn-primary btn-sm"><img src="images/metamask-icon-32.png" /> Login</a>
                          </div>
                          <div class="dark-light-toggle theme-switch" v-if="wrongNetwork">
                              <strong>Wrong network! Switch to SmartBCH.</strong>
                          </div>
                          <div class="dropdown profile_log dropdown">
                              <div data-bs-toggle="dropdown" aria-haspopup="true" class="" aria-expanded="false">
                                  <div class="user icon-menu active loggeed-in-addr-text"><span>{{myAddressDisplay}}</span>
                                  </div>
                              </div>
                              <div tabindex="-1" role="menu" aria-hidden="true" class="dropdown-menu dropdown-menu-right">
                                  <a class="dropdown-item" href="./pixelguy">
                                      <span><i class="ri-user-line"></i></span>My PGC NFTs
                                  </a>
                                  <a class="dropdown-item" href="./shorai">
                                      <span><i class="ri-sword-line"></i></span>My Shorai NFTs
                                  </a>
                                  <a class="dropdown-item" v-bind:href="'https://www.smartscan.cash/address/' + stakingAddress ">
                                      <span><i class="ri-eye-line"></i></span>View on Explorer
                                  </a>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
  </div>
  <div class="sidebar">
      <div class="brand-logo"><a class="full-logo" href="./"><img src="images/logoi.png" alt="" width="30"></a></div>
      <div class="menu">
          <ul>
              <li>
                  <a href="./pixelguy">
                      <span><i class="ri-user-fill"></i></span>
                      <span class="nav-text">Pixelguy</span>
                  </a>
              </li>
            <li>
                  <a href="./shorai">
                      <span><i class="ri-sword-fill"></i></span>
                      <span class="nav-text">Shorai</span>
                  </a>
              </li>
              <!--li>
                  <a href="./admin">
                      <span><i class="ri-settings-3-fill"></i></span>
                      <span class="nav-text">Admin</span>
                  </a>
              </li-->
          </ul>
      </div>
  </div>
</template>
<script>
    import {ethers} from "ethers";
    export default {
      data() {
        return {
          myAddress: null,
          myAddressDisplay: null,
          stakingAddress: window.StakingAddress,
          shorai: window.ShoraiAddress,
          pixelguy: window.PixelguyAddress,
          wrongNetwork: false,
        }
      },
      mounted: function() {
        this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
        this.provider.getNetwork().then((network) => {
          if(network.chainId !== window.netId) {
            this.wrongNetwork = true;
          }
        });
        this.provider.on("network", (n, o) => {
          if (o) { window.location.reload(); }
        });
        this.signer = this.provider.getSigner();
        this.signer.getAddress().then((address) => {
          this.myAddress = address;
          this.myAddressDisplay = address.substring(0, 5) + '...' + address.substring(address.length - 5, address.length);
        });
      },
      methods: {
        loginMM: function() {
          this.provider.send("eth_requestAccounts", []).then(() => {
            this.signer = this.provider.getSigner();
            this.signer.getAddress().then((address) => {
              this.myAddress = address;
              this.myAddressDisplay = address.substring(0, 5) + '...' + address.substring(address.length - 5, address.length);
            });
          });
        },
        addToMM: function() {
          window.ethereum.request({
            method: 'wallet_watchAsset',
            params: {
              type: 'ERC20',
              options: {
                address: window.TokenAddress,
                symbol: 'NFTC',
                decimals: 18
              },
            },
          });
        }
      }
    }
</script>
