<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <h5 class="mb-0">Admin</h5>
          <p>Earnable tokens: {{liquidity}}</p>
          <p>Total allocated power: {{allocatedPower}}</p>
          <p>Released tokens by contract: {{released}}</p>
          <p>Total staking rate: {{earningRate}} / hour</p>
          <p>
            <button @click="stakeAdminAddLiquidity" class="btn btn-primary">Add 36M supply</button>
          </p>
          <p>
            Mint Pixelguy:
            <button @click="mintPixelguy(1)" class="btn btn-primary">Mint 1</button>&nbsp;
            <button @click="mintPixelguy(20)" class="btn btn-primary">Mint 20</button>
          </p>
          <p>
            Mint Shorai:
            <button @click="mintShorai(1)" class="btn btn-primary">Mint 1</button>&nbsp;
            <button @click="mintShorai(20)" class="btn btn-primary">Mint 20</button>
          </p>
          <p>
            <button @click="stakeAdminUnpause" class="btn btn-success">Unpause</button>&nbsp;
            <button @click="stakeAdminPause" class="btn btn-warning">Pause</button>
          </p>
          <p>
            <button @click="resetBoosts" class="btn btn-primary">Reset boosts</button>
          </p>
        </div>
        <div>
          <h1>Power settings</h1>
          <p>
            <span v-for="n in 50" :key="n" >
              <button @click="powerSetterPXC((n-1) * 200)" class="btn btn-primary">Power up Pixelguy {{((n-1)*200)+1}} - {{(n*200)}}</button>&nbsp;
            </span>
          </p>
          <p>
            <span v-for="n in 50" :key="n">
              <button @click="powerSetterShorai((n-1) * 200)" class="btn btn-primary">Power up Shorai {{((n-1)*200)+1}} - {{(n*200)}}</button>&nbsp;
            </span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {ethers} from "ethers";
import SimpleToken from "../../artifacts/contracts/SimpleToken.sol/SimpleToken.json";
import NftPower from "../../artifacts/contracts/NftPower.sol/NftPower.json";
import NftStake from "../../artifacts/contracts/NftStake.sol/NftStake.json";
import SimpleNft from "../../artifacts/contracts/SimpleNFT.sol/SimpleNFT.json";

import shoraiRarities from '../shorai-rarity.json';
import pixelguyRarities from '../pixelguy-rarity.json';


let TokenAddress = window.TokenAddress;
let PowerAddress = window.PowerAddress;
let StakingAddress = window.StakingAddress;

export default {
  name: 'App',
  components: {},
  data() {
    return {
      'token': null,
      'nft': null,
      'staking': null,
      'power': null,
      'allocatedPower': null,
      'released': null,
      'liquidity': null,
      'earned': null,
      'unallocatedEarned': null,
      'earningRate': null,
    }
  },
  methods: {
    stakeAdminAddLiquidity: async function () {
      let tx = await this.staking.addLiquidity('36000000000000000000000000');
      await tx.wait();
      window.location.reload();
    },
    stakeAdminPause: async function () {
      let tx = await this.staking.pause();
      await tx.wait();
      window.location.reload();
    },
    stakeAdminUnpause: async function() {
      let tx = await this.staking.unpause();
      await tx.wait();
      window.location.reload();
    },
    mintShorai: async function(amount) {
      let c = new ethers.Contract(window.ShoraiAddress, SimpleNft.abi, this.provider.getSigner());
      let tx = await c.mint(amount);
      await tx.wait();
      window.location.reload();
    },
    mintPixelguy: async function(amount) {
      let c = new ethers.Contract(window.PixelguyAddress, SimpleNft.abi, this.provider.getSigner());
      let tx = await c.mint(amount);
      await tx.wait();
      window.location.reload();
    },
    resetBoosts: async function() {
      let tx = await this.staking.resetBoosts();
      await tx.wait();
      window.location.reload();
    },
    powerSetterPXC: async function(start) {
      let size = 200;
      let tx = await this.power.bulkSet(window.PixelguyAddress, pixelguyRarities.tokenId.slice(start, start+size), pixelguyRarities.power.slice(start, start+size));
      await tx.wait();
    },
    powerSetterShorai: async function(start) {
      console.log(start);
      let size = 200;
      let tx = await this.power.bulkSet(window.ShoraiAddress, shoraiRarities.tokenId.slice(start, start+size), shoraiRarities.power.slice(start, start+size));
      await tx.wait();
    },
  },
  mounted: function() {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();
      let tokenContract = new ethers.Contract(TokenAddress, SimpleToken.abi, this.provider.getSigner());
      let powerContract = new ethers.Contract(PowerAddress, NftPower.abi, this.provider.getSigner());
      let stakingContract = new ethers.Contract(StakingAddress, NftStake.abi, this.provider.getSigner());

      this.token = tokenContract;
      this.staking = stakingContract;
      this.power = powerContract;
    }).then(() => {
      this.staking.earnableTokens().then((amount) => {
        this.liquidity = ethers.utils.formatEther(amount);
      });
      this.staking.allocatedPower().then((power) => {
        this.allocatedPower = power;
      });
      this.staking.released().then((amount) => {
        this.released = ethers.utils.formatEther(amount);
      });
      this.staking.getEarningPerHour().then((amount) => {
        this.earningRate = ethers.utils.formatEther(amount);
      });
    })
  }
}
</script>